*{
  margin: 0;
  padding: 0;
  box-sizing: border-box;
  font-family: "Poppins", sans-serif;
}

/* Our Variables */

:root{
  --bgcolor: #f1f5fd;
  --primarycolor: #6f5de7;
  --textcolor: #828286;
  --maintextcolor: #333333;
}

/* common styling */

html{
  font-size: 62.5%;
}

a{
  text-decoration: none;
  color: white;
}

h1, h2, h3, h4, h5, h6{
  color: #333333;
}

/* navbar styling css */

.navbar-bg{
  background-color: var(--bgcolor);
  padding: 2rem 0;
  z-index: 99;
  position: relative;
}

.navbar-brand{
  font-size: 2.5rem;
}

.navbar li{
  padding: 0 1.2rem;
}

.navbar li a{
  font-size: 1.6rem;
  font-weight: lighter;
}

.navbar li a:hover{
  color: var(--primarycolor) !important;
  text-shadow: 0 0.1rem 0.3rem rgba(183, 171, 255, 0.5);
}

.btn-style{
  width: 8rem;
  height: 4rem;
  border: 1px solid #333333;
  border-radius: 10px;
  font-size: 1.6rem;
  color: #333333;
  text-transform: capitalize;
  margin: 0 5px;
  transition: .5ms;
}

.btn-style:hover{
  background-color: var(--primarycolor);
  color: white;
  border: none;
  transition: .5ms;
}

/* header section styling css */

header{
  background-color: var(--bgcolor);
  min-height: 100vh;
  width: 100%;
}

.main-hero-container{
  height: 80vh;
  display: flex;
}

.main-hero-container h1{
  text-transform: capitalize;
  font-weight: bolder;
}

.main-hero-para{
  font-size: 1.5rem;
  color: var(--textcolor);
  margin: 3rem 0 5rem 0;
  font-weight: lighter;
  width: 75%;
}

.main-hero-container h3{
  font-weight: lighter;
}

.input-group{
  position: relative !important;
}

.form-control-text{
  position: absolute;
  height: 5rem;
  border: none;
  outline: none;
  padding-left: 1.5rem !important;
  font-size: 1.4rem;
  font-weight: lighter;
}

.input-group-button{
  position: absolute;
  right: 27%;
  height: 4rem;
  background-color: var(--primarycolor);
  color: white;
  font-size: 1.6rem;
  border-radius: 2rem !important;
  text-align: center;
  display: flex;
  align-items: center;
  justify-content: center;
  top: 0.5rem;
  width: 15%;
  cursor: pointer;
}



.main-herosection-images{
  position: relative;
}

.main-herosection-images img{
  width: 60%;
  height: 75%;
  margin-top: 10rem;
  box-shadow: 0 0.8rem 0.8rem rgba(0,0,0,0.25);
  border-radius: 1.5rem;
  position: absolute;
  /* opacity: 0.5; */
  cursor: pointer;
  transition-duration: 0.2s;
}

.main-herosection-images img:hover{
  /* opacity: 1; */
  width: 62%;
  height: 77%;
}

.main-herosection-images .main-hero-img2{
  position: absolute;
  width: 50%;
  height: 30%;
  left: 55%;
  top: -2%;
  box-shadow: 0 0.8rem 0.8rem rgba(0,0,0,0.25);
}

.main-herosection-images .main-hero-img2:hover{
  width: 52%;
  height: 32%;
}

/* how it work container styling css */
.work-container{
  width: 80vw;
  padding: 5rem;
  box-shadow: 0 0.4rem 1.2rem rgba(0,0,0,0.4);
  margin-top: -10rem;
  background-color: #fff;
  border-radius: 0.5rem;
}

.work-container h1{
  margin-bottom: 5rem;
  font-size: 3rem;
  font-weight: bolder;
  word-spacing: 1rem;
}

.work-container-subdiv{
  padding: 0 6rem;
}

.work-container .sub-heading{
  font-size: 2rem;
}

.fontawesome-style{
  font-size: 2rem;
  color: var(--primarycolor);
  /* margin-bottom: 2rem; */
  width: 6rem;
  height: 6rem;
  border-radius: 50%;
  background-color:var(--bgcolor);
  display: grid;
  place-items: center;
  text-align: center;
  margin: 2rem auto;
}

/* About Us section css styling */

.common-section {
  margin: 5rem 0;
}

.our-services img{
  width: 40%;
  height: 75%;
  margin-top: 10rem;
  box-shadow: rgba(0,0,0,0.25) 0px 0.8rem 0.8rem 0.8rem;
  border-radius: 1.5rem;
}

.our-services .mini-title{
  text-transform: uppercase;
  font-weight: normal;
  margin: 3rem 0;
  text-align: left;
  font-size: 1.5rem;
}

.our-services .main-heading{
  font-weight: bold;
}

.our-services-info{
  margin-top: 4rem;
}  

.our-services-info .our-services-number{
  width: 3rem;
  height: 3rem;
  border-radius: 50%;
  border: 2px solid var(--primarycolor);
  background-color: transparent;
  font-size: 1.6rem;
  color: var(--primarycolor);
  display: grid;
  place-items: center;
}

.our-service-data {
  padding-left: 2rem;
}

.our-services .our-service-data p{
  margin: 1rem 0;
}

/* about us 2nd section */

.our-services-rightside{
  margin-top: 10rem;
}

.our-services-rightside-content{
  padding-left: 15rem;
}

/* service container css styling */

.service-main-container{
  margin-top: 10rem;
  padding: 5rem 0;
}

.service-main-container .main-heading{
  margin: 3rem 0;
}

.work-container-subdiv{
  text-align: center;
  box-shadow: 0 0.1rem 0.4rem rgba(0,0,0,0.4);
}

.service-para{
  font-size:1.5rem;
  color: var(--textcolor);
  margin: 3rem 0;
}

.work-container-subdiv:hover{
  transition-duration: 0.2s;
  cursor: pointer;
  background-color: var(--bgcolor);
}

.work-container-subdiv:hover > .fontawesome-style{
  background-color: #fff;
}

@media (min-width: 1400px){
  .service-container .col-xxl-4{
    flex: 0 0 auto;
    width: 30% !important;
    margin: auto;
  }
}

@media (min-width:992px){ 
  .service-container .col-lg-4{
    flex: 0 0 auto;
    width: 30% !important;
    margin: 2.5rem auto;
  }
}

/* contact section css styling */

.contactus-section {
  margin: 15rem 0;
}

.contact-leftside img {
  min-width: 80%;
  max-height: 37rem;
  height: auto;
  cursor: pointer;
}

.contact-input-feild {
  margin-bottom: 4rem;
}

.contact-rightside form .form-control {
  border: 1px solid #ced4da;
  -webkit-appearance: none;
  height: 5rem;
  border-radius: 0.5rem;
  font-size: 1.6rem;
  padding-left: 1rem;
  font-weight: lighter;
}

.contact-rightside form .form-check-input {
  width: 2rem;
  height: 2rem;
}

.form-checkbox-style {
  margin: 4rem 0;
}

.contact-rightside form .main-hero-para {
  font-size: 1.5rem;
  color: var(--text-color);
  margin: 0;
  font-weight: lighter;
  width: 75%;
  padding: 0 2rem;
}

/* Footer section css styling */

footer {
  padding: 5rem 0;
  background-color: #182334;
  color: #fff;
}

footer h2 {
  color: #fff;
}

footer li {
  list-style: none;
  font-size: 1.5rem;
  font-weight: lighter;
  color: #fff;
  cursor: pointer;
  margin: 1rem 0;
}

footer li:hover {
  color: var(--primary-color);
}

footer .fontawesome-style {
  font-size: 2.3rem;
  color: var(--primary-color);
  background-color: transparent;
  width: 0rem;
  height: 0rem;
  margin: 2rem 0;
  cursor: pointer;
}

footer .fontawesome-style:hover {
  color: #fff;
}

/* Error Page CSS Styling */

#notfound {
  position: relative;
  height: 100vh;
}

#notfound .notfound {
  position: absolute;
  left: 50%;
  top: 50%;
  -webkit-transform: translate(-50%, -50%);
  -ms-transform: translate(-50%, -50%);
  transform: translate(-50%, -50%);
}

.notfound {
  max-width: 920px;
  width: 100%;
  line-height: 1.4;
  text-align: center;
  padding-left: 15px;
  padding-right: 15px;
}

.notfound .notfound-404 {
  position: absolute;
  height: 100px;
  top: 0;
  left: 50%;
  -webkit-transform: translateX(-50%);
  -ms-transform: translateX(-50%);
  transform: translateX(-50%);
  z-index: -1;
}

.notfound .notfound-404 h1 {
  font-family: "Maven Pro", sans-serif;
  color: #ececec;
  font-weight: 900;
  font-size: 276px;
  margin: 0px;
  position: absolute;
  left: 50%;
  top: 50%;
  -webkit-transform: translate(-50%, -50%);
  -ms-transform: translate(-50%, -50%);
  transform: translate(-50%, -50%);
}

.notfound h2 {
  font-family: "Maven Pro", sans-serif;
  font-size: 46px;
  color: #000;
  font-weight: 900;
  text-transform: uppercase;
  margin: 0px;
}

.notfound p {
  font-size: 16px;
  color: #000;
  font-weight: 400;
  text-transform: uppercase;
  margin-top: 15px;
}

.notfound a {
  font-size: 14px;
  text-decoration: none;
  text-transform: uppercase;
  background: #189cf0;
  display: inline-block;
  padding: 16px 38px;
  border: 2px solid transparent;
  border-radius: 40px;
  color: #fff;
  font-weight: 400;
  -webkit-transition: 0.2s all;
  transition: 0.2s all;
}

.notfound a:hover {
  background-color: #fff;
  border-color: #189cf0;
  color: #189cf0;
}

@media only screen and (max-width: 480px) {
  .notfound .notfound-404 h1 {
    font-size: 162px;
  }
  .notfound h2 {
    font-size: 26px;
  }
}

/* responsive media queries */


@media (min-width: 1500px) {
  .container,
  .container-lg,
  .container-md,
  .container-sm,
  .container-xl,
  .container-xxl {
    max-width: 1500px !important;
  }
}

@media (min-width: 1400px) {
  .container,
  .container-lg,
  .container-md,
  .container-sm,
  .container-xl,
  .container-xxl {
    max-width: 1400px;
  }
}